const DEFAULT_REALTIME_MODEL = "gpt-4o-realtime-preview-2025-06-03";

// Allow a few friendly aliases while ensuring we always return a model string
// that is accepted by the Realtime API today.
const MODEL_ALIASES: Record<string, string> = {
  "gpt-realtime": DEFAULT_REALTIME_MODEL,
  "gpt-4o-realtime-preview": DEFAULT_REALTIME_MODEL,
  "gpt-4o-realtime-preview-latest": DEFAULT_REALTIME_MODEL,
};

export function resolveRealtimeModel(rawModel?: string | null): string {
  if (!rawModel) return DEFAULT_REALTIME_MODEL;
  const trimmed = rawModel.trim();
  if (!trimmed) return DEFAULT_REALTIME_MODEL;
  const normalizedKey = trimmed.toLowerCase();
  return MODEL_ALIASES[normalizedKey] ?? trimmed;
}

export { DEFAULT_REALTIME_MODEL };
